.navigation__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navigation__link{
  display: flex;
  padding: 10px;
  background-color: #000;

  color: #fff;
}

.active {
  background-color: #0000ff;
}

.phone__galery {
  width: 100px;
  border: 1px solid black;
  margin-right: 10px;
  padding: 10px;
  background-color: #fff;

  cursor: pointer;
  transition: all 0.6s;
}

.phone__preview {
  width: 100%;
}

.phone__card-list {
  display: flex;
  flex-wrap: wrap;

  list-style: none;
  margin: 0;
  padding: 0;
  max-width: 1200px;
}

.phone__card-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  box-sizing: border-box;
  margin: 10px;
  padding: 20px;
  width: 220px;

  box-shadow: 0 0 10px #d3d3d3;
}

.phone__buttons {
  display: flex;
  justify-content: space-between;
}

.button {
  padding: 15px 20px;
  color: #fff;
  background-color: #3232e2;
  border: none;

  text-transform: uppercase;
}

.button:hover {
  background-color: #2222db;
  cursor: pointer;
}


